  --Gallery--columns-mobile: 6;
  --Gallery--columns: var(--Gallery--columns-desktop);
  --Gallery--rows-mobile: 10;
  --Gallery--rows-desktop: 10;
  --Gallery--rows: var(--Gallery--rows-desktop);
  --Gallery--img-ratio: 1.5;
  --Gallery--gap: .4rem;
  --Gallery--img-width: calc((100vw - (var(--Gallery--columns) * var(--Gallery--gap))) / var(--Gallery--columns));
  --Gallery--img-height: calc(var(--Gallery--img-width) * var(--Gallery--img-ratio));
  --Gallery--tile-shift: calc(var(--Gallery--img-height) * .15);
  grid-template-columns: repeat(var(--Gallery--columns), 1fr);
  gap: var(--Gallery--gap);
  margin-top: calc(var(--Gallery--tile-shift) * -1);
  height: calc(var(--Gallery--rows) * var(--Gallery--img-height)  + var(--Gallery--rows) * var(--Gallery--gap));
  display: grid;
  overflow: hidden;
