@import 'mixins';
@import 'colors';

.Gallery__header {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 3rem 2.6rem 3rem 3rem;
  justify-content: space-between;
  align-items: start;
  z-index: 2;

  @include mobile(){
    padding: 2.5rem;
  }

  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 18rem;
    background: linear-gradient(0deg, transparent, black);
    z-index: -1;
    pointer-events: none;
  }
}

.Gallery__logo {
  width: 16.4rem;
  margin-top: 0.5rem;
  filter: drop-shadow(0 0.2rem 0.3rem rgba(#000, 0.2));

  @include mobile(){
    width: 14rem;
    margin-top: 1rem;
  }
}

.Gallery__button {
  box-shadow: 0 0.2rem 0.3rem rgba(#000, 0.2);
}

.Gallery__list {
  --Gallery--columns-desktop: 6;
  --Gallery--columns-mobile: 6;
  --Gallery--columns: var(--Gallery--columns-desktop);

  --Gallery--rows-mobile: 10;
  --Gallery--rows-desktop: 10;
  --Gallery--rows: var(--Gallery--rows-desktop);

  --Gallery--img-ratio: 1.5;
  --Gallery--gap: 0.4rem;

  --Gallery--img-width: calc((100vw - (var(--Gallery--columns) * var(--Gallery--gap))) / var(--Gallery--columns));
  --Gallery--img-height: calc(var(--Gallery--img-width) * var(--Gallery--img-ratio));
  --Gallery--tile-shift: calc(var(--Gallery--img-height) * 0.15);

  @include mobile(){
    --Gallery--columns: var(--Gallery--columns-mobile);
    --Gallery--rows: var(--Gallery--rows-mobile);
  }

  display: grid;
  grid-template-columns: repeat(var(--Gallery--columns), 1fr);
  gap: var(--Gallery--gap);
  margin-top: calc(var(--Gallery--tile-shift) * -1);
  height: calc(var(--Gallery--rows) * var(--Gallery--img-height) + var(--Gallery--rows) * var(--Gallery--gap));
  overflow: hidden;

  img {
    width: 100%;
    height: var(--Gallery--img-height);
    object-fit: cover;
    display: block;
    margin: 0;
    opacity: 0;
    transition: opacity 400ms;
    cursor: pointer;

    &.loaded {
      opacity: 1;
    }

    &:nth-child(odd) {
      transform: translateY(var(--Gallery--tile-shift));
    }
  }
}

.Gallery__popup {
  --bs-modal-bg: #1a1515;
  --bs-modal-border-width: 0;
  --bs-modal-width: 50rem;

  background: rgba(#000000, 0.75);
}

.Gallery__popup-content {
  border-radius: 1rem;
  overflow: hidden;
  box-shadow: 0 1rem 2rem rgba(#000000, 0.5);
}


.Gallery__popup-close {
  --bs-btn-close-white-filter: none;
  --bs-btn-close-opacity: 1;

  width: 3.4rem;
  height: 3.4rem;
  border-radius: 100%;
  backdrop-filter: blur(0.6rem);

  position: absolute;
  top: 1.5rem;
  right: 1.5rem;

  background: {
    color: rgba(#FFFFFF, 0.2);
    image: url('../assets/images/icon-close.svg') !important;
    repeat: no-repeat;
    position: center;
    size: 42%;
  };
}

.Gallery__popup-img {
  width: 100%;
  display: block;
}

.Gallery__popup-title {
  position: absolute;
  bottom: 1rem;
  right: 1rem;
  padding: 0.5rem 0.9rem;
  text-shadow: 0 0.1rem 0.2rem rgba(#000000, 0.2);
  font-size: 1.4rem;
  backdrop-filter: blur(0.6rem);
  background: rgba(#FFFFFF, 0.2);
  border-radius: 0.5rem;
  display: flex;
  align-items: end;
  justify-content: center;

  @include mobile(){
    font-size: 1.8rem;
  }
}
